import styled from "styled-components";

export const Wrapper = styled.div`
  position: fixed;
  width: 100%;
  z-index: 200;
  top: 0;
  height: 100vh;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;

  @media all and (min-width: 768px) {
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    position: fixed;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 200;
  }
  @media all and (min-width: 1366px) {
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    position: fixed;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 200;
  }
`;

export const Container = styled.div`
  background: #fff;
  

  width: 400px;
  height: 100px;
  min-height: 400px;
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.2);
  background: #fcfcfc;
  position: fixed;

  display: flex;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr;
  position: relative;

  @media (min-width: 410px) and (max-width: 513px) {
    width: 400px;
    min-height: 400px;
  }
  @media all and (min-width: 768px) {
    width: 600px;
    height: 100px;
    min-height: 550px;
    box-shadow: 0 5px 16px rgba(0, 0, 0, 0.2);
    background: #fcfcfc;
    position: fixed;

    display: flex;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr;
    position: relative;
  }

  @media all and (min-width: 1366px) {
    width: 600px;
    height: 100px;
    min-height: 550px;
    box-shadow: 0 5px 16px rgba(0, 0, 0, 0.2);
    background: #fcfcfc;
    position: fixed;

    display: flex;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr;
    position: relative;
  }
`;

export const Icon = styled.div`
  cursor: pointer;
  font-size: 2rem;
  color: #662583;
  height: 80px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 20px;
  width: 40px;
  float: right;
  height: 40px;
  position: absolute;
  top: 10px;
  right: 0;
  z-index: 5;

  @media all and (min-width: 1366px) {
    cursor: pointer;
    font-size: 2rem;
    color: #662583;
    height: 80px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 40px;
    width: 40px;
    float: right;
    height: 40px;
    position: absolute;
    top: 10px;
    right: 0;
    z-index: 5;
  }
`;
export const ImageWrapper = styled.div`
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;

  img {
    width: 249.98px;
    height: 239px;
    object-fit: cover;

    &.adjust {
      width: 320px !important;
      height: 269px !important;
    }
  }
`;

export const RightWrapper = styled.div`
  height: 100%;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  position: absolute;
  right: 0px;
  top: 0px;
  z-index: 2;
  .rightarr {
    z-index: -2;
    cursor: pointer;
  }
  @media (min-width: 410px) and (max-width: 513px) {
    right: 0px;
    top: 0px;
  }

  @media all and (max-width: 514px) {
    right: 10px;
    top: 0px;
  }
  @media all and (min-width: 1366px) {
    right: 70px;
    top: 0px;
  }
`;

export const LeftWrapper = styled.div`
  height: 100%;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 2;
  font-size: 30px;
  .leftarr {
    z-index: -2;
    cursor: pointer;
  }

  @media (min-width: 410px) and (max-width: 513px) {
   left: 0px;
    top: 0px;
  }
  @media all and (max-width: 514px) {
    left: 10px;
    top: 0px;
  }
  @media all and (min-width: 1366px) {
    left: 70px;
    top: 0px;
  }
`;





export const TextList = styled.h3`
  color: #bababa;
  margin-top: 10px;
`;
