import React, { useRef, useEffect, useState } from "react";
import { MdClose } from "react-icons/md";
//import { useSpring, animated } from "react-spring";

import { AiOutlineArrowLeft, AiOutlineArrowRight } from "react-icons/ai";

import {
  Wrapper,
  Container,
  ImageWrapper,
  Icon,
  TextList,
  RightWrapper,
  LeftWrapper,
} from "./style";

const ColorPopup = ({color, showModal, setShowModal, colorlist}) => {
    const [colorID, setColorID] = useState()
    const [colorLists, setColorLists] = useState({});
    const popRef = useRef();
    useEffect(()=>{
        setColorID(parseInt(color))
        colorlist.map((data, index)=>{
            if (data.id === color){
               return setColorLists(data);
            } else {
                return null;
            }
        })
    }, [colorlist, color]);

    const leftClick = ()=>{
      let colorId = colorID - 1;
      
      let lengthList = colorlist.length;

      if (colorId >= 0 && colorId <= lengthList){
        setColorID(colorId)
      colorlist.map((data, index) => {
        if (data.id === colorID) {
          return setColorLists(data);
        } else {
          return null;
        }
      }); 
      } else {
        return null;
      }
     
    };

    const rightClick = ()=>{
  let colorId = colorID + 1;
  
  let lengthList = colorlist.length;

  if (colorId >= 0 && colorId <= lengthList) {
    setColorID(colorId);
    colorlist.map((data, index) => {
      if (data.id === colorID) {
        return setColorLists(data);
      } else {
        return null;
      }
    });
  } else {
    return null;
  }
    }

    const closePopup = (e) => {
      if (popRef.current === e.target) {
        setShowModal(false);
      }
    };
  return (
    <>
      {showModal ? (
        <Wrapper ref={popRef} onClick={closePopup}>
          <Container>
            <Icon onClick={() => setShowModal((prev) => !prev)}>
              <MdClose />
            </Icon>
            <ImageWrapper>
              <img src={colorLists.link} alt="" />
              <TextList>{colorLists.hex}</TextList>
              <RightWrapper onClick={rightClick}>
                <AiOutlineArrowRight
                  className="rightarr"
                  onClick={rightClick}
                />
              </RightWrapper>
              <LeftWrapper onClick={leftClick}>
                <AiOutlineArrowLeft onClick={leftClick} className="leftarr" />
              </LeftWrapper>
            </ImageWrapper>
          </Container>
        </Wrapper>
      ) : null}
    </>
  );
};

export default ColorPopup;
