import React from 'react'
import {
  Wrapper,
  Container,
  ListContainer,
  ListWrap,
  ListText,
} from "./substyles";
import {HeadWrap} from '../../common';
const SubLists = ({ Colordata, colorlist, openModal }) => {
  
  return (
    <Wrapper>
      <Container>
        <HeadWrap Colordata={Colordata} />

        <ListContainer>
          {colorlist.map((data, index) => {
            return (
              <ListWrap key={index} id={data.id} onClick={openModal}>
                <img src={data.link} id={data.id} alt="color"/>
                <ListText id={data.id}>{data.hex}</ListText>
              </ListWrap>
            );
          })}

          
        </ListContainer>
      </Container>
    </Wrapper>
  );
};

export  {SubLists};
